
html{
  background-color: grey;
  text-align: center;
  display: flex;
  justify-content: center;
}
body{
  background-color: green;
  border-radius: 5px;
  display: block;
  flex-direction: row;
  justify-contents: center;
  align-items: center;
  width: 400px;
  height: 600px;
  text-align: center;
  font-size: 30px;
  color: white;
}
#main-title{
  font-size: 40px;
}
#time-left{
  font-size: 90px;
  
}
#break-length, #session-length{
  padding: 0px 20px 0px 20px;
}
#timer-container{
  border: 5px solid white;
  border-radius: 20px;
  margin: 10px 40px 10px 40px;
  padding: 0px 10px 0px 10px;
}
#break-decrement, #break-increment, #session-decrement, #session-increment, #start_stop, #reset{
  font-size: 35px;
  padding: 0px 15px 0px 15px;
  background-color: white;
  border-radius: 10px;
}

